<template>
  <view class="register-container">
    <view class="header">
      <text class="welcome-text">Ê≥®ÂÜåÊñ∞Ë¥¶Âè∑</text>
      <text class="sub-text">Ê¨¢ËøéÂä†ÂÖ•ÈÖíÂ∫óÈ¢ÑËÆ¢Âπ≥Âè∞</text>
    </view>

    <view class="form-group">
      <view class="input-item">
        <text class="icon-text">üë§</text>
        <input 
          class="input" 
          type="text" 
          v-model="registerForm.username" 
          placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç (Âª∫ËÆÆÂ≠óÊØç+Êï∞Â≠ó)" 
          placeholder-style="color: #c0c4cc;"
        />
      </view>

      <view class="input-item">
        <text class="icon-text">üîí</text>
        <input 
          class="input" 
          type="password" 
          v-model="registerForm.password" 
          placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å (Ëá≥Â∞ë6‰Ωç)" 
          placeholder-style="color: #c0c4cc;"
        />
      </view>

      <view class="input-item">
        <text class="icon-text">üîê</text>
        <input 
          class="input" 
          type="password" 
          v-model="confirmPassword" 
          placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å" 
          placeholder-style="color: #c0c4cc;"
        />
      </view>

      <view class="input-item">
        <text class="icon-text">üõ°Ô∏è</text>
        <input 
          class="input" 
          type="text" 
          v-model="registerForm.code" 
          placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" 
          placeholder-style="color: #c0c4cc;"
          maxlength="4"
        />
        <view class="captcha-box" @click="getCodeImage">
          <image v-if="captchaImg" :src="captchaImg" class="captcha-img" mode="aspectFit"></image>
          <text v-else style="font-size: 24rpx; color: #999;">Âä†ËΩΩ‰∏≠...</text>
        </view>
      </view>
    </view>

    <view class="agreement-box" @click="toggleAgree">
      <view class="checkbox" :class="{ 'checked': isAgree }">
        <text v-if="isAgree" class="check-icon">‚úì</text>
      </view>
      <text class="agree-text">ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè</text>
      <text class="link" @click.stop="goProtocol">„ÄäÁî®Êà∑ÊúçÂä°ÂçèËÆÆ„Äã</text>
    </view>

    <button 
      class="login-btn" 
      :class="{ 'btn-active': isReadyToRegister }" 
      @click="handleRegister"
    >
      Á´ãÂç≥Ê≥®ÂÜå
    </button>
    
    <view class="bottom-links">
      <text class="link-text" @click="goLogin">Â∑≤ÊúâË¥¶Âè∑ÔºüÁÇπÂáªÂéªÁôªÂΩï</text>
    </view>
  </view>
</template>

<script>
import http from '@/utils/request.js'

export default {
  data() {
    return {
      registerForm: {
        username: '',
        password: '',
        code: '',
        uuid: ''
      },
      confirmPassword: '', // Á°ÆËÆ§ÂØÜÁ†ÅÂçïÁã¨ÊãøÂá∫Êù•ÂÅöÂâçÁ´ØÊ†°È™å
      captchaImg: '',
      isAgree: false
    };
  },
  computed: {
    // ÂøÖÂ°´È°πÈÉΩÂ°´‰∫ÜÔºå‰∏îÂãæÈÄâ‰∫ÜÂçèËÆÆÔºåÊåâÈíÆÊâç‰∫ÆËµ∑
    isReadyToRegister() {
      return this.registerForm.username.length > 0 && 
             this.registerForm.password.length > 0 && 
             this.confirmPassword.length > 0 && 
             this.registerForm.code.length > 0 && 
             this.isAgree;
    }
  },
  onLoad() {
    this.getCodeImage(); // Âä†ËΩΩÈ™åËØÅÁ†Å
  },
  methods: {
    toggleAgree() {
      this.isAgree = !this.isAgree;
    },
    
    // Ëé∑ÂèñÈ™åËØÅÁ†Å (ÂíåÁôªÂΩïÈ°µ‰∏ÄÊ†∑)
    getCodeImage() {
      http.get('/captchaImage').then(res => {
        if (res.code === 200) {
          this.captchaImg = 'data:image/gif;base64,' + res.img;
          this.registerForm.uuid = res.uuid;
        } else {
          uni.showToast({ title: 'Ëé∑ÂèñÈ™åËØÅÁ†ÅÂ§±Ë¥•', icon: 'none' });
        }
      });
    },

    // Êèê‰∫§Ê≥®ÂÜå
    handleRegister() {
      // 1. ÂâçÁ´ØË°®ÂçïÈò≤ÂëÜÊ†°È™å
      if (!this.isAgree) {
        return uni.showToast({ title: 'ËØ∑ÂÖàÈòÖËØªÂπ∂ÂãæÈÄâÂ∫ïÈÉ®ÂçèËÆÆ', icon: 'none' });
      }
      if (this.registerForm.username.length < 2 || this.registerForm.username.length > 20) {
        return uni.showToast({ title: 'Áî®Êà∑ÂêçÈïøÂ∫¶ÈúÄÂú®2-20‰πãÈó¥', icon: 'none' });
      }
      if (this.registerForm.password.length < 6 || this.registerForm.password.length > 20) {
        return uni.showToast({ title: 'ÂØÜÁ†ÅÈïøÂ∫¶ÈúÄÂú®6-20‰πãÈó¥', icon: 'none' });
      }
      if (this.registerForm.password !== this.confirmPassword) {
        return uni.showToast({ title: '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥', icon: 'none' });
      }

      uni.showLoading({ title: 'Ê≥®ÂÜå‰∏≠...' });
      
      // 2. Ë∞ÉÁî®Ëã•‰æùËá™Â∏¶ÁöÑ /register Êé•Âè£
      http.post('/register', this.registerForm).then(res => {
        uni.hideLoading();
        
        if (res.code === 200) {
          // Ê≥®ÂÜåÊàêÂäüÔºÅ
          uni.showModal({
            title: 'Ê≥®ÂÜåÊàêÂäü',
            content: 'ÊÇ®ÁöÑË¥¶Âè∑Â∑≤Ê≥®ÂÜåÊàêÂäüÔºåËØ∑ÂâçÂæÄÁôªÂΩï„ÄÇ',
            showCancel: false,
            success: () => {
              // ËøîÂõûÁôªÂΩïÈ°µ
              uni.navigateBack();
            }
          });
        } else {
          // Ê≥®ÂÜåÂ§±Ë¥• (ÊØîÂ¶ÇÁî®Êà∑ÂêçÈáçÂ§ç„ÄÅÈ™åËØÅÁ†ÅÈîôËØØ)
          uni.showToast({ title: res.msg || 'Ê≥®ÂÜåÂ§±Ë¥•', icon: 'none' });
          this.getCodeImage(); // Âà∑Êñ∞È™åËØÅÁ†Å
          this.registerForm.code = ''; 
        }
      }).catch(err => {
        uni.hideLoading();
        uni.showToast({ title: err.msg || 'ÊúçÂä°Âô®ÂºÇÂ∏∏', icon: 'none' });
        this.getCodeImage();
      });
    },

    goProtocol() {
      uni.showToast({ title: 'ÂçèËÆÆÂä†ËΩΩ‰∏≠...', icon: 'none' });
    },
    goLogin() {
      // ËøîÂõû‰∏ä‰∏ÄÈ°µ(ÁôªÂΩïÈ°µ)
      uni.navigateBack();
    }
  }
};
</script>

